import React from 'react';
import { Link } from 'react-router-dom';
import NavHelper from '../../helper/NavHelper';
import navigation from '../../data/navigation.json';

class Header extends NavHelper {
    render() {
        const stickyHeader = this.state.stickyHeader ? ' sticky' : '';
        return (
            <header className="header">
                {/* Topbar */}
                <div className="topbar bg-custom-blue">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-6">
                                <div className="left-side">
                                    <ul className="custom-flex">
                                        <li>
                                            <Link to="#" className="text-custom-white">
                                                <i className="fab fa-facebook-f" />
                                            </Link>
                                        </li>
                                        <li>
                                            <Link to="#" className="text-custom-white">
                                                <i className="fab fa-twitter" />
                                            </Link>
                                        </li>
                                        <li>
                                            <Link to="#" className="text-custom-white">
                                                <i className="fab fa-instagram" />
                                            </Link>
                                        </li>
                                        <li>
                                            <Link to="#" className="text-custom-white">
                                                <i className="fab fa-linkedin" />
                                            </Link>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div className="right-side">
                                    <ul className="custom-flex">
                                        <li>
                                            <Link to="#" className="text-custom-white">Login</Link>
                                        </li>
                                        <li>
                                            <Link to="#" className="text-custom-white">Signup</Link>
                                        </li>
                                        <li>
                                            <select>
                                                <option>USD</option>
                                                <option>INR</option>
                                                <option>EURO</option>
                                            </select>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Topbar */}
                {/* Navigation */}
                <div className={"navigation" + stickyHeader}>
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <div className="main-navigation">
                                    <div className="logo">
                                        <Link to="/">
                                            <img src={process.env.PUBLIC_URL + "/assets/images/toor-logo.png"} className="img-fluid image-fit" alt="img" />
                                        </Link>
                                    </div>
                                    <div className={this.state.navMethod === true ? 'main-menu active' : 'main-menu'}>
                                        <div className="logo">
                                            <Link to="/">
                                                <img src={process.env.PUBLIC_URL + "/assets/images/toor-logo.png"} className="img-fluid image-fit" alt="img" />
                                            </Link>
                                        </div>
                                        <nav>
                                            <ul className="custom-flex">
                                                {navigation.length > 0 ? navigation.map((item, i) => (
                                                    <li key={i} className={`menu-item ${item.child ? 'menu-item-has-children' : ''} `} onClick={this.triggerChild}>
                                                        {item.child ? <Link onClick={e => e.preventDefault()} to="/" className="text-light-dark"> {item.linkText} <span className="arrow" /></Link> : <Link to={item.link} className="text-light-dark"> {item.linkText} </Link>}
                                                        {item.child ?
                                                            <ul className="custom sub-menu" role="menu">
                                                                {item.submenu.map((sub_item, i) => (
                                                                    <li key={i} className={`menu-item ${sub_item.child ? 'menu-item-has-children' : ''} `}>
                                                                        {sub_item.child ? <Link onClick={e => e.preventDefault()} to="/" className="text-custom-white"> {sub_item.linkText} <span className="arrow" /></Link> : <Link to={sub_item.link} className="text-custom-white"> {sub_item.linkText} </Link>}
                                                                        {sub_item.submenu ?
                                                                            <ul className="custom sub-menu">
                                                                                {sub_item.submenu.map((third_item, i) => (
                                                                                    <li className="menu-item" key={i}><Link
                                                                                        to={third_item.link} className="text-custom-white">{third_item.linkText}</Link>
                                                                                    </li>
                                                                                ))}
                                                                            </ul> : null}
                                                                    </li>
                                                                ))}
                                                            </ul>
                                                            : null
                                                        }
                                                    </li>
                                                )) : null}
                                                {/* Data */}
                                            </ul>
                                        </nav>
                                        <div className="cta-btn">
                                            <Link to="/booking" className="btn-first btn-submit">Enquiry Now</Link>
                                        </div>
                                    </div>
                                    <div className="hamburger-menu" onClick={this.toggleNav}>
                                        <div className={this.state.navMethod === true ? 'menu-btn active' : 'menu-btn'}>
                                            <span />
                                            <span />
                                            <span />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Navigation */}
            </header>
        );
    }
}

export default Header;